<style scoped>

</style>
<template>
  <div>
    <h3>修改密码</h3>
    <Divider orientation="left">填写密码</Divider>
    <Row>
      <Col :xs="24" :sm="24" :md="12" :lg="12">
        <Form :label-width="120">
          <FormItem label="新密码:">
            <Input v-model="formData.password" placeholder="请填写密码" type="password"></Input>
          </FormItem>
          <FormItem label="确认新密码:">
            <Input v-model="formData.confirm_pass" placeholder="请填写确认密码" type="password"></Input>
          </FormItem>
          <FormItem>
            <Button type="success" long @click="submit">提交</Button>
          </FormItem>
        </Form>
        <Spin size="large" fix v-if="load"></Spin>
      </Col>
    </Row>

  </div>
</template>

<script>
import api from './api'
export default {
  data () {
    return {
      formData: {},
      load: false
    }
  },
  mounted () {

  },
  methods: {
    submit () {
      this.load = true
      api.pwd(this.formData, () => {
        this.load = false
        this.$Message.success('修改密码成功')
        this.$router.push({ name: 'login' })
      }, () => {
        this.load = false
      })
    }
  }
}
</script>
